import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Checkbox from '../../../components/ui/Checkbox';
import Icon from '../../../components/ui/Icon';
import iLensConsultationProduct from '../../../interface/lens-consultation-product.interface';
import lensConsultationSlice, { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const LensConsultationProduct : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation-product');
    const { lensBrand, lensProducts, lensType } = useSelector(getLensConsultation);
    const dispatch = useDispatch();
    const [product, setProduct] = useState<iLensConsultationProduct | null>();
    const [subProduct, setSubProduct] = useState<iLensConsultationProduct | null>();


    // HOOKS
    useEffect(() => {
        if(subProduct) {
            dispatch(lensConsultationSlice.actions.setSliderPage(0))
        }
    }, [subProduct])

    // METHODS
    // const back = () => {
    //     dispatch(lensConsultationSlice.actions.setSliderPage(1));
    // }

    const selectProduct = (p: iLensConsultationProduct) => {
        setSubProduct(null);   
        if(product?.value === p.value) {
            setProduct(null);
            return;
        }
        setProduct(p)
    }

    const saveProductsState = (p: iLensConsultationProduct) => {
        const newState = {
            ...lensProducts,
            [lensType?.type || '']: {
                product: product,
                subProduct: p
            }
        }
        dispatch(lensConsultationSlice.actions.selectLensProduct(newState))
    }

    const selectSubProduct = (p: iLensConsultationProduct) => {
        if(subProduct?.value === p.value) {
            setSubProduct(null);
            return;
        }    
        setSubProduct(p);
        saveProductsState(p);
    }

    const isSelectedProduct = (p: iLensConsultationProduct) => {
        return p.value === product?.value;
    }
    
    const isSelectedSubProduct = (p: iLensConsultationProduct) => {
        return p.value === subProduct?.value;
    }

    const renderSubProducts = (product: iLensConsultationProduct) => {
        if (!product.subProducts)return null;
        return (
            <div className={E('sub-list', [isSelectedProduct(product) ? 'show' : ''])}>
                <ul>
                    {
                        product.subProducts.map( p => (
                            <li 
                                key={p.value}
                                onClick={()=> selectSubProduct(p)}
                                className={E('sub-list-item', [isSelectedSubProduct(p) ? 'selected' : ''])}>
                                {p.label}                     
                                <Checkbox checked={isSelectedSubProduct(p)}/>
                            </li>
                        ))
                    }
                </ul>
            </div>

        )
    }

    return (
        <div className={B()}>
            <ul className={E('list')}>
                {
                    lensBrand?.products.map( p => (
                        <li 
                            key={p.value}                                
                            className={E('list-item', [isSelectedProduct(p) ? 'selected' : ''])}>
                            <div onClick={()=> selectProduct(p)}>
                                {p.label}
                                <Icon name="down-arrow"/>
                            </div>
                            {renderSubProducts(p)}
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}

export default LensConsultationProduct;