import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import productsSlice, { getProducts } from '../../../store/slices/products.slice';
import useBEM from '../../../utils/hooks/useBEM';
import Arrow from '../../ui/Arrow';
import Icon from '../../ui/Icon';
import SubProducts from './SubProducts';

interface Props {
    id: string,
    label: string,
    background: string,
    logo?: string,
    subProducts: any[],
    backgroundType: 'png' | 'jpg'
}

const ProductCard : React.FC<Props>  = ({
    id,
    label,
    background,
    logo,
    subProducts,
    backgroundType
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('product-card');
    const dispatch = useDispatch();
    const  { brandToDemo } = useSelector(getProducts);

    // HOOKS

    // METHODS
    const selectBrand = () => {
        const product = (brandToDemo && brandToDemo.id === id) ? undefined :
        {
            id,
            label,
            products: subProducts
        } 

        dispatch(productsSlice.actions.selectBrandTodemo(product))
    }

    const isOpen = () => {
        return brandToDemo && brandToDemo.id === id;
    }

    return (
        <li className={B()}>                                        
            <Icon
                extension={backgroundType}
                className={E('background')}
                name={background} 
                type="image" 
                background={true}/>

            {
                logo && 
                <Icon 
                    className={E('logo')}
                    name={logo} 
                    type={id === 'BlueUV_Filter_System' ? 'image' : 'svg' }
                />
            }
            
            
            <div className={E('see-more', [ isOpen()? 'open' : ''])} >
                <div className={E('see-more-head')} onClick={selectBrand}>    
                    <label>
                        See more <Icon name="eye" type="image"/>
                    </label>
                    <label>{label} <Arrow type="down" color="#fff"/></label>
                </div>

                <SubProducts 
                    products={subProducts}                
                />    
            </div>     

                                           
        </li>
    )
}

export default ProductCard;