import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from '../../components/Modal';
import Button from '../../components/ui/Button';
import InputText from '../../components/ui/InputText';
import routes from '../../constants/routes.constant';
import userSlice, { getUser } from '../../store/slices/user.slice';
import withAnimationTransition from '../../utils/hoc/withAnimationTransition';
import useBEM from '../../utils/hooks/useBEM';
import LensProposaBuilderlExperience from './components/LensProposaBuilderlExperience';
import LensProposalBuilderCard from './components/LensProposalBuilderCard';
import LensProposalBuilderFooter from './components/LensProposalBuilderFooter';
import useIndexedDB from '../../utils/hooks/useIndexedDB';
import { getNeedExplorer } from '../../store/slices/need-explorer.slice';

interface Props {

}

const LensProposalBuilder : React.FC<Props>  = () =>  {
    // VARIABLES
    const { prescription } = useSelector(getUser);
    
    const [B, E] = useBEM(routes.lensProposalBuilder.id);
    const [isExperience, setIsExperience] = useState(false);
    const [modifyId, setModifyId] = useState(false);

    const [modifyIDField, setModifyIDField] = useState<string>('');

    const {
        isGeneralDone
    } = useSelector(getNeedExplorer);
    
    const dispatch = useDispatch();
    const { id } = useSelector(getUser);
    const [connect, get] = useIndexedDB();

    const { needExplorer, measures, lensConsultation, delivery } = routes

    // HOOKS
    useEffect(() => {
        connect();
        console.log(prescription.values.sphere.od);
    }, [])

    useEffect(() => {
        setModifyIDField(id || '');
    }, [id])

    // METHODS
    const footerActionListener = (action: string) => {
        if(action === 'id') 
            setModifyId(true);
        else 
            setIsExperience(!isExperience);
    }

    const lensConsultationFooter = () => {
        return <label className="impacted">
            Impacted by: <span>Need Explorer</span> <span>Prescription</span>
        </label> 
    }

    const modalOnClose = () => {
        setModifyId(false);
    }

    const idOnchange = (event: any) => {
        setModifyIDField(event.target.value);
    }

    const editId = async () => {
        const user = await get('userStore',String(modifyIDField));
        if(!modifyIDField || modifyIDField.length < 6 || !user){
            alert('You have entered an invalid  or not existing ID, Please Try Again');
            return;
        }        
        dispatch(userSlice.actions.setUserData(user));
        setModifyId(false);        
    }

    const IsModuleCompleted = (module: string) => {
        // Validate here if any of the module is completed and return it's appropriate properties
        var completed = { icon: 'pencil', completed: true }
        var notCompleted = { icon: 'plus', completed: false }

        switch(module) {
            case needExplorer.label: return isGeneralDone ? completed : notCompleted
            default: return notCompleted
        }
   
    }
    const closeExperience = () => {
        if (isExperience) {
            setIsExperience(!isExperience)
        }
    }

    const isPrescriptionComplete = () => {
        return prescription.values.sphere.od ? true : false
    }


    return (
        <div className={B()}>
            
            <div className={E('main', [isExperience ? 'experienced' : ''])}>     
                
                <LensProposalBuilderCard route={needExplorer} label={needExplorer.label} icon="need-explorer" completed={IsModuleCompleted(needExplorer.label).completed}/>
                <LensProposalBuilderCard route={routes.prescription} label="Prescription" icon="prescription" completed={isPrescriptionComplete()}/>
                <LensProposalBuilderCard route={measures} label="Measures" icon="measures"/>
                <LensProposalBuilderCard route={lensConsultation} label="Lens Consultation" icon="lens-consultation" footer={lensConsultationFooter()}/>
                <LensProposalBuilderCard route={delivery} label="Delivery" icon="delivery" className="delivery"/>



                <LensProposalBuilderFooter isExperience={isExperience} onActionTrigger={footerActionListener}/>
                <div className={E('overlay')} onClick={closeExperience}/>
            </div> 

            <LensProposaBuilderlExperience/>


            <Modal show={modifyId} onClose={() => modalOnClose()}>
                <div className="home__start-from-id-popup">
                    <h2>Modify ID</h2>
                    
                    <div>
                        <InputText
                            value={modifyIDField}
                            placeholder="Enter an ID" 
                            maxlength={25} 
                            onChange={idOnchange}/>
                        
                        <Button text="Next" icon="right-arrow-circle-white" click={editId}/>                         
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default withAnimationTransition(LensProposalBuilder, {
    entrance: 'FADE_IN'
});
