import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import packages from '../../constants/package-offer.constants';
import iPackageOfferStore from '../interface/package-offer-store-interface';
import iRootState from '../interface/root-state.interface';

const initialState: iPackageOfferStore  = {
    data: packages,
}

const packageOfferSlice = createSlice({
    name: 'packageOffer',
    initialState,
    reducers: {
        setPackageOfferData: (state, action: PayloadAction<any>) => {
            const { correct, protect, enhance } = action.payload

            const validateProducts = (cat: any) => {
                return cat.product && cat.subProduct !== null ? cat.product.packageOffersDowngrade : [{label: null}, {label: null}, {label: null}]
            }

            const correctDowngrade = validateProducts(correct)
            const protectDowngrade = validateProducts(protect)
            const enhanceDowngrade = validateProducts(enhance)

            const newData = []
            console.log(action.payload)

            // For Personalized (index=0), For Optimized (index=1), For Standart (index=2)

            for (var i = 0; i < 3; i++) {
                let temp = {
                    ...packages[i],
                    product: {
                        ...packages[i].product,
                        name: correctDowngrade[i].label
                    },
                    categories: [
                        correctDowngrade[i],
                        protectDowngrade[i],
                        enhanceDowngrade[i],
                    ]
                }

                newData.push(temp)
            }
            
            return {
                ...state,
                data: newData
            }
        },
    }
});

export const getPackageOffer = (state:iRootState) => state.packageOffer;
export default packageOfferSlice;