import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from '../../../components/Modal';
import Accordion from '../../../components/ui/Accordion';
import Button from '../../../components/ui/Button';
import Checkbox from '../../../components/ui/Checkbox';
import InputText from '../../../components/ui/InputText';
import { lensThickness } from '../../../constants/my-companion.constants';
import iMyCompanionItem from '../../../interface/my-companion-subitem.interface';
import myCompanionSlice, { getMyCompanion } from '../../../store/slices/mycompanion.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

 }


const MyCompanionList : React.FC<Props>  = () =>  {
    // VARIABLES
    const { data, isPriceOff,  category} = useSelector(getMyCompanion);
    const [selectedThickness, setSelectedThickness] = useState<number[]>([])
    const [itemPrice, setItemPrice] = useState<iMyCompanionItem>()
    const [show, setShow] = useState(false)
    const [catIndex, setCatIndex] = useState(0)
    const [subCatIndex, setSubCatIndex] = useState(0)
    
    // HOOKS
    const [B, E] = useBEM('mycompanion');
    const dispatch = useDispatch();

    // METHODS
    const priceContent = (price: number) => {
        if(isPriceOff) return null;
        return <span className="my-companion-list__price">
                    {price} €
               </span>;
    }

    const showCategoryLens = () => {
        return category === 'correct' ||
        category === 'enhance' ||
        category === 'protect'
    }

    const showLensThickness = () => {
        return category === 'thickness'
    }

    const onLensThicknessSelect = (expanded: boolean, thickness: number) => {
        let newValue;
        if(expanded) {
            newValue = [...selectedThickness, thickness];
        } else {
            newValue = selectedThickness.filter( x => x !== thickness);
        }
        setSelectedThickness(newValue)
    }

    const handleClick = (subCat?: any) => {
        setShow(!show)
        if(subCat){
            setItemPrice(subCat)
        }
    }

    const handleSubmit = () => {
       const newData:any = data.map( (firstLevelData: any, firstLevelIndex: number) => {
            if(catIndex === firstLevelIndex){   
                const subItems = firstLevelData.subItems.map((subItem: any, subItemIndex: number) => {
                    if(subItemIndex === subCatIndex) return { ...subItem, price: itemPrice?.price }
                    return subItem;
                });
                return { ...firstLevelData, subItems }
            }
            return firstLevelData
        });
        dispatch(myCompanionSlice.actions.selectPrice(newData));
        setShow(!show)
    }

    const priceOnChange = (event: any) => {
        if(isNaN(+event.target.value)) {return}
        setItemPrice({...itemPrice!, price: +event.target.value})
    }
 
    return (
        
        <div className="my-companion-list">
            {   
                showCategoryLens() && 
                data.map( (cat, index) =>  (
                    <Accordion title={cat.label} //rightTitle={priceContent(cat.price)} 
                        key={index}
                    >
                        {
                            cat.subItems.map( (subCat, subIndex) => (
                                <Accordion title={subCat.label} type="child" rightTitle={priceContent(subCat.price)} key={subIndex}
                                rightTitleClicked={() => {handleClick(subCat); setCatIndex(index); setSubCatIndex(subIndex)}}/>
                            ))
                        }
                    </Accordion>
                ))
            }

            {
                showLensThickness() &&
                lensThickness.map( (thickness, index ) => (
                    <Accordion 
                        clicked={(expanded) => onLensThicknessSelect(expanded, thickness)}
                        title={String(thickness)} 
                        key={index} 
                        rightTitle={<Checkbox checked={selectedThickness.includes(thickness)}/>}/>
                ))
            }

            <Modal show={show} onClose={() => {handleClick()}}>
                <div className={E('change-price-popup')}>
                    <h2>Enter Price</h2>
                    
                     <div>
                        <InputText 
                            value={itemPrice?.price}
                            placeholder="Enter new price" 
                            maxlength={6}
                            onChange={priceOnChange}/>
                        
                        { 
                            itemPrice?.price && 
                            <Button text="Next" icon="right-arrow-circle-white" click={handleSubmit}/> 
                        }                        
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default MyCompanionList;