import React from 'react';
import Icon from '../../components/ui/Icon';
import useBEM from '../../utils/hooks/useBEM';

interface Props {

}

const Emat : React.FC<Props>  = () =>  {
    // VARIABLES
    const files = [
        { filename: 'Filename_01', dateCreated: 'March 10 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_01' },
        { filename: 'Filename_02', dateCreated: 'March 11 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_02' },
        { filename: 'Filename_03', dateCreated: 'March 12 2021', type: 'video', size: '10MB', id: 'ESSILOR_03' },
        { filename: 'Filename_04', dateCreated: 'March 13 2021', type: 'image', size: '10MB', id: 'ESSILOR_04' },
        { filename: 'Filename_05', dateCreated: 'March 10 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_01' },
        { filename: 'Filename_06', dateCreated: 'March 11 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_02' },
        { filename: 'Filename_07', dateCreated: 'March 12 2021', type: 'video', size: '10MB', id: 'ESSILOR_03' },
        { filename: 'Filename_08', dateCreated: 'March 13 2021', type: 'image', size: '10MB', id: 'ESSILOR_04' },
        { filename: 'Filename_09', dateCreated: 'March 10 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_01' },
        { filename: 'Filename_10', dateCreated: 'March 11 2021', type: 'pdf', size: '10MB', id: 'ESSILOR_02' },
        { filename: 'Filename_11', dateCreated: 'March 12 2021', type: 'video', size: '10MB', id: 'ESSILOR_03' },
        { filename: 'Filename_12', dateCreated: 'March 13 2021', type: 'image', size: '10MB', id: 'ESSILOR_04' }
    ];

    const fileSort = [
        { label: 'All Media', sortKey: 'all', selected: true},
        { label: 'Photos', sortKey: 'image', selected: false},
        { label: 'Videos', sortKey: 'video', selected: false},
        { label: 'PDFs', sortKey: 'pdf', selected: false},
    ]

    // HOOKS
    const [Bfs, Efs] = useBEM('file-sort');
    const [Bfc, Efc] = useBEM('file-container');

    // METHODS
    const renderFileSort = () => {
        let sort = fileSort.map((sort,key) => {
            return (
                <li key={key} className={Efs('item', [sort.selected ? 'active' : ''])} >
                    <label>{sort.label}</label>
                </li>
            )
        })

        return sort
    }

    const renderFiles = () => {
        let file = files.map((file,key) => {
            return (
                <li key={key} className={Efc('item')}>
                    <Icon name="flip" className="icon-file"/>
                    <label className="file-name">{file.filename}</label>
                    <label className="date-created">{file.dateCreated}</label>
                    <Icon name="flip" className="icon-view"/>
                </li>
            )
        })

        return file
    }


    return (
        <div className="emat">
            <div className="emat-container">
                <div className={Bfs()}>
                    <ul className={Efs('items')}>
                        {renderFileSort()}
                    </ul>
                </div>
                <div className={Bfc()}>
                    <div>
                        <label>{`${files.length.toString()} files`}</label>       
                    </div>
                    <ul className={Efc('items')}>
                        {renderFiles()}
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Emat;