import React from 'react';
import Icon from '../../../components/ui/Icon';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    price: string,
    name?: string | null,
    selected: boolean,
    categories: Array<Object>
}

const PackageProduct : React.FC<Props>  = ({
    price,
    name,
    selected,
    categories
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('package-product');

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            <div className={E('product')}>
                <span>{price}</span>
                <h4>{name}</h4>

                { selected && <Icon name="check"/> }
                { !selected && <div></div> }
                
            </div>

            <ul className={E('lenses')}>
                {
                    categories.map((i:any, k:any)=>{
                        return i.label && (
                            <li key={k}>
                            <span></span>
                            <label>{i.label}</label>
                            </li> 
                        )
                    })

                }
            </ul>

            <span className={E('glass')}>* Le verre</span>
        </div>
    )
}

export default PackageProduct;