import React, { Fragment, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import Modal from '../../../components/Modal';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import routes from '../../../constants/routes.constant';
import iLensConsultationType from '../../../interface/lens-consultation-type.interface';
import lensConsultationSlice, { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';
import useUtils from '../../../utils/hooks/useUtils';
import protectColorList from '../../../constants/lens-consultation-protect-color.constants';

interface Props {

}

const LensConsultationType : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-consultation-type');
    const { cloneObject } = useUtils();
    const { data, lensProducts, protectColor } = useSelector(getLensConsultation);
    const [ validatePackageOfferModal, setValidatePackageOfferModal ] = useState(false);
    const dispatch = useDispatch();
    const history = useHistory();
    const [showColorBox, setShowColorBox] = useState(true)

    // HOOKS

    // METHODS
    const selectType = (type: iLensConsultationType) => {
        dispatch(lensConsultationSlice.actions.selectLensType(type));
        dispatch(lensConsultationSlice.actions.setSliderPage(1));
    }

    const getSavedValue = (type: 'correct' | 'protect' | 'enhance') => {
        if(lensProducts){
            return lensProducts[type];            
        }
    }

    const deleteValue = (type: string) => {
        const newState = {
            ...lensProducts,
            [type]: {
                product: null,
                subProduct: null
            }
        }
        dispatch(lensConsultationSlice.actions.selectLensProduct(newState))
        dispatch(lensConsultationSlice.actions.selectProtectColor(null))
    }

    const ColorPicker = (value: any) => {
        //const filteringValue = value.value.product.value;
       const results = protectColorList.filter(function (name:any) { return name.name === value.value.product.value; })[0]?.color
        if (results){
            return (
            <Fragment>
                {results.map((color: any) => { 
                    return (
                        <span className={`color-picker ${protectColor === color.name ? 'active' : ''}`}
                            style={{backgroundColor: color.value}}
                            key={color.name}
                            title={color.label}
                            onClick={() => {dispatch(lensConsultationSlice.actions.selectProtectColor(color.name))}}/>
                    )
                })} 
            </Fragment>  
            );
        }
        return <></>
    }

    const renderListItem = (type: iLensConsultationType) => {
        const savedValue = getSavedValue(type.type as 'correct');
        const currentValue = savedValue?.subProduct?.label

        return (
            <li className={E('list-item', [currentValue ? 'with-value' : ''])} >
                <h2 onClick={() => selectType(type)}> 
                    <Icon name={type.type} type="image"/>
                    {type.label} 
                </h2>

                { 
                    !currentValue &&  
                    <Button  
                        click={() => selectType(type)} 
                        icon="plus"                          
                        type="secondary" 
                        circle={true}/> 
                }

                {
                    currentValue && 
                    <div className={E('list-item-value')}>
                        {currentValue}
                        <div>
                            <Icon name="trash" click={() => deleteValue(type.type)}/>
                            {type.type === 'protect' && <Icon name="plus-white" click={() => selectType(type)}/>}
                        </div>
                    </div>
                }

                {
                    type.type === 'protect' && currentValue &&
                    <div className='color-picker-menu'>
                        <Icon name={`${showColorBox ? 'close-white' : 'expand-white'}`} click={() => setShowColorBox(!showColorBox)}/>
                        {showColorBox && <ColorPicker value={savedValue}/>}
                    </div>
                }
            </li>
        )
    }

    const withLensProductsSelected = () => {
        let withProducts = false;
        const cloned = cloneObject<any>(lensProducts);
        ['correct','protect','enhance'].forEach( (key: any) => {
            if(cloned[key].subProduct) withProducts = true;
        });
        return withProducts;
    }

    const gotoPackageOffer = () => {
        const proceed = withLensProductsSelected();
        if(!proceed) {
            setValidatePackageOfferModal(true);
            return;
        }
        history.push(routes.packageOffer.path);
    }

    return (
        <div className={B()}>

            <ul className={E('list')}>
                <li className={E('list-item')} onClick={gotoPackageOffer}>
                    Package Offer 
                    <Icon name="stacked-group2"/>
                </li>
                {
                    data.map( type => (
                        <Fragment key={type.type} >
                            {renderListItem(type)}   
                        </Fragment>
                    ))
                }
            </ul>


            <Modal show={validatePackageOfferModal} height="32.5rem" onClose={() => setValidatePackageOfferModal(false)}>
                <div className={E('modal')}>
                    <div className={E('modal-icon')}>
                        <Icon name="exclamation"/>
                    </div>
                    <p className={E('modal-text')}>To have access to Packaged Offer<br/> you must fill in at least one information.</p>
                    <Button 
                        click={() => setValidatePackageOfferModal(false)}
                        text="Fill the Lens Consultation" 
                        type="secondary" weight="normal"/>
                </div>
            </Modal>
        </div>
    )
}

export default LensConsultationType;