import React, { useState } from 'react';
import Modal from '../../components/Modal';
import Button from '../../components/ui/Button';
import Icon from '../../components/ui/Icon';
import MaterialInput from '../../components/ui/MateriaTextlnput';
import routes from '../../constants/routes.constant';
import useBEM from '../../utils/hooks/useBEM';
import useGoto from '../../utils/hooks/useGoto';

interface Props {

}

const PracticeID : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('practice-id');
    const [showSaveModal, setShowSaveModal] = useState(false);
    const goto = useGoto();


    // HOOKS

    // METHODS
    const onSave = () => {
        setShowSaveModal(true);
        
        goto.route(routes.home, 2000);
    }

    return (
        <div className={B()}>
            
            <div className={E('image')}/>
            <div className={E('form')}>
                

                <div className={E('form-section')}>
                    <h2>Global Information</h2>                    
                    <MaterialInput placeholder="Name: Store’s name" label="Store Name"/>
                    <MaterialInput placeholder="Phone: Type here" label="Phone"/>
                    <MaterialInput placeholder="Address: Type here" label="Address"/>
                    <MaterialInput placeholder="Mail Address: Type here" label="Mail Address"/>
                </div>


                <div className={E('form-section')}>
                    <h2>License Numbers</h2>
                    <MaterialInput placeholder="Essilor Companion: Type here" label="Essilor Companion"/>
                    <MaterialInput placeholder="Essilor Companion VR: Type here" label="Essilor Companion VR"/>
                    <Button 
                        click={onSave}
                        text="Save" 
                        icon="right-arrow-circle-white" 
                        className={E('form-save')}/>
                </div>

            </div>

            <Modal 
                onClose={() => setShowSaveModal(false)}
                height="23rem" width="26rem"
                show={showSaveModal} hideCloseButton={true}>
                
                <div className="my-companion-footer__saved">
                    <Icon name="saved"></Icon>
                    <h2>Saved</h2>
                </div>

            </Modal>
        </div>
    )
}

export default PracticeID;