import React from 'react';
import iNeedExplorerQuestion from '../../../../interface/need-explorer-question.interface';

interface Props {
    question: iNeedExplorerQuestion,
    onAnswer?: (answer: string) => void
}

const Note : React.FC<Props>  = ({
    question,
    onAnswer
}) =>  {
    // VARIABLES

    // HOOKS

    // METHODS
    const onChangeHandler = (event: any) => {
        if(onAnswer) onAnswer(event.target.value);
    }

    return (
        <textarea 
            value={question.answer as string}
            className="note" placeholder={question.placeholder} onChange={onChangeHandler}>
        </textarea>
    )
}

export default Note;