import React from 'react';
import iQuestionChoice from '../../../../interface/question-option.interface';
import useBEM from '../../../../utils/hooks/useBEM';
import ReactHtmlParser from 'react-html-parser';
import Icon from '../../../../components/ui/Icon';
import iNeedExplorerQuestion from '../../../../interface/need-explorer-question.interface';
import iQuestionOption from '../../../../interface/question-option.interface';

interface Props {
    question: iNeedExplorerQuestion,
    onAnswer?: (answer: iQuestionOption[]) => void,
}

const ChoiceBoxImage : React.FC<Props>  = ({
    question,
    onAnswer
}) =>  {
    // VARIABLES
    const  [B, E] = useBEM('choice-box-image');

    // HOOKS

    // METHODS
    const getAnswer = () => {
        if(!question.answer)return [];
        return question.answer as iQuestionOption[];
    }

    const select = (choice: iQuestionChoice) => {
        const isExist = getAnswer()?.filter(c => c.value === choice.value);
        let finalAnswer;
        if( isExist.length > 0) {
            finalAnswer = getAnswer()?.filter(c => c.value !== choice.value);
        } else {
            finalAnswer = [ ...getAnswer(), choice ]
        }
        if(onAnswer) onAnswer(finalAnswer);
    }

    const selectedChoiceClassName = (choice: iQuestionChoice) => {
        const isSelected = getAnswer()?.filter(c => c.value === choice.value)
        return [isSelected.length > 0 ? 'selected' : '']
    }

    return (
        <ul className={B()}>
            {
                question.options && question.options.map(option => (
                    <li 
                        key={option.value}
                        className={E('item', selectedChoiceClassName(option))}                     
                        onClick={() => select(option)}>
                        
                        <Icon type="image" name={option.image || ''} className={E('image')} background={true}/>

                        <span className={E('check')}></span>

                        <label>{ReactHtmlParser(option.label)}</label>
                    </li>
                ))
            }            
        </ul>
    )
}

export default ChoiceBoxImage;