import React from 'react';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import routes from '../../../constants/routes.constant';
import iRoute from '../../../interface/route.interface';
import useBEM from '../../../utils/hooks/useBEM';
import useGoto from '../../../utils/hooks/useGoto';

interface Props {
    label: string,
    icon: string,
    completed?: boolean,
    className?: string,
    footer?: React.ReactNode,
    route?: iRoute,
}

const LensProposalBuilderCard : React.FC<Props>  = ({
    label,
    icon,
    completed = false,
    className = '',
    footer,
    route
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-proposal-builder-card');
    const goto = useGoto();

    // HOOKS

    // METHODS
    const classModifiers = () => {
        const classes = [];
        if(completed) classes.push('completed');
        if(footer) classes.push('with-footer');
        return classes
    }

    const gotoRoute = () => {
        if (!route)return;
        
        goto.routeWithTransition(
            routes.lensProposalBuilder,
            route,
            'FADE_OUT',
            800
        );
    }

    return (
        <div className={B(classModifiers()) + ` ${className}`} onClick={gotoRoute}>
            <div className={E('info')}>
                <h2>
                    <Icon name={icon}/>
                    {label}
                </h2>

                { completed && <span>Completed</span> }
            </div>
            <Button icon={completed ? 'pencil' : 'plus'} type="secondary"  circle={true}/>

            { footer && footer}
        </div>
    )
}

export default LensProposalBuilderCard;