import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import Button from '../../components/ui/Button';
import Icon from '../../components/ui/Icon';
import InputText from '../../components/ui/InputText';
import routes from '../../constants/routes.constant';
import licenseSlice from '../../store/slices/license.slice';
import useBEM from '../../utils/hooks/useBEM';
import useGoto from '../../utils/hooks/useGoto';

interface Props {

}

const LicenseActivation : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('license-activation');
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const dispatch = useDispatch();
    const goto = useGoto();

    // HOOKS

    // METHODS
    const connect = () => {
        dispatch(licenseSlice.actions.updateStatus('active'));
        goto.route(routes.home, 1000);
    }

    return (
        <div className={B()}>

            <div className={E('form')}>


                <Icon name="logo" type="image" className={E('logo')}/>

                <h2 className={E('title')}>Welcome to<br/>your new companion !</h2>

                <div className={E('fields')}>
                    <InputText placeholder="ID" type="light-gray" value={id} onChange={ v => setId(v.target.value)}/>
                    <InputText inputType="password" placeholder="Password" type="light-gray" value={password} onChange={ v => setPassword(v.target.value)}/>
                </div>

                <Button 
                    click={() => connect()}
                    text="Connect" 
                    icon="right-arrow-circle-white" 
                    className={E('connect')} 
                    disabled={ (id && password) ? false : true }/>

            </div>    
        
        </div>
    )
}

export default LicenseActivation;