import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import RouterLink from '../../../components/ui/RouterLink';
import NeedExplorerViewType from '../../../types/need-explorer-view.type';
import needExplorerSlice, { getNeedExplorer } from '../../../store/slices/need-explorer.slice';
import useBEM from '../../../utils/hooks/useBEM';
import userSlice from '../../../store/slices/user.slice';
import lensConsultationSlice from '../../../store/slices/lens-consultation.slice';
import productLogic from '../../../constants/product-logic.constants';
import productLogicTemplate from '../../../constants/product-logic-template.constants';
import lensConsultationData from '../../../constants/lens-consultation-data.constants';

interface Props {

}

const NeedExplorerGeneralCheckPoint : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('need-explorer-general-checkpoint');
    const dispatch = useDispatch();
    const currentNeedExplorerData = useSelector(getNeedExplorer);

    // // HOOKS

    // // METHODS
    const modify = () => {        
        dispatch(needExplorerSlice.actions.questionaireFromStart());
        dispatch(needExplorerSlice.actions.changeView(NeedExplorerViewType.QUESTIONNAIRE));
    }

    const gotoAnamnesis = () => {
        //Save User Data for Need Explorer
        dispatch(userSlice.actions.saveNeedExplorerData(currentNeedExplorerData)); 

        //Evaluate Decision Tree to prefilled data in Lens Consultation
        const resultObject = evaluateDecisionTree()

        const temp = {
            data: lensConsultationData,
            sliderPage: 0,
            lensType: null,
            protectColor: null,
            lensProducts: resultObject
        }

        dispatch(userSlice.actions.savePrefilledData(temp)); 
        dispatch(lensConsultationSlice.actions.setPrefilledDataFromNeedExplorer(temp))

        //Move to Anamnesis Page
        dispatch(needExplorerSlice.actions.changeView(NeedExplorerViewType.ANAMNESIS));
    }

    const goToQuestionnaire = () => {
        // Continue answering questions 
        dispatch(needExplorerSlice.actions.changeView(NeedExplorerViewType.QUESTIONNAIRE));
    }

    const evaluateDecisionTree = () => {
        const generalQuestion = currentNeedExplorerData.questions[0].questions

        //Single Selections
        const answerIDs = generalQuestion
            .filter((q:any) => !Array.isArray(q.answer))
            .map((q:any) => q.answer.value)

        //Multiple Selections
        generalQuestion
            .filter((q:any) => Array.isArray(q.answer))
            .filter((q:any) => q.type !== 'SLIDER_GROUP')
            .filter((q:any) => q.type !== 'DRAG_AND_DROP_SORTER')
            .map((a:any) => {
                a.answer.map((v: any) => { answerIDs.push(v.value) } )
            })

        //Filter product matrix
        const matrix = []
        for (var i = 0; i < answerIDs.length; i++) {
            var item  = productLogic.find(m => m.answerId === answerIDs[i]); 
            matrix.push(item?.scoreMatrix)
        }

        console.log("-----",AddMatrixValues(matrix))

        // Get the highest score for Correct Category
        const correctScores = AddMatrixValues(matrix).filter((x:any, i: number) => { return i < 4 })
        const indexOfHighScore_correct = correctScores.reduce((iMax: any, x: any, i:any, arr:any) => x > arr[iMax] ? i : iMax, 0);

        // Get the highest score for Protect Category
        const protectScores = AddMatrixValues(matrix).filter((x:any, i: number) => { return i >= 4 && i <= 5 })
        const indexOfHighScore_protect = protectScores.reduce((iMax: any, x: any, i:any, arr:any) => x > arr[iMax] ? i : iMax, 0);
 
        // Get the highest score for Enhance Category
        const enhanceScores = AddMatrixValues(matrix).filter((x:any, i: number) => { return i >= 6 && i <= 8 })
        const indexOfHighScore_enhance = enhanceScores.reduce((iMax: any, x: any, i:any, arr:any) => x > arr[iMax] ? i : iMax, 0);

        console.log(indexOfHighScore_correct)
        console.log(indexOfHighScore_protect+4)
        console.log(indexOfHighScore_enhance+6)

        return{
            correct: {
                product: productLogicTemplate[indexOfHighScore_correct].product,
                subProduct: extractSubProduct(productLogicTemplate[indexOfHighScore_correct].product.subProducts)
            },
            protect: {
                product: productLogicTemplate[indexOfHighScore_protect+4].product,
                subProduct: extractSubProduct(productLogicTemplate[indexOfHighScore_protect+4].product.subProducts)
            },
            enhance: {
                product: productLogicTemplate[indexOfHighScore_enhance+6].product,
                subProduct: extractSubProduct(productLogicTemplate[indexOfHighScore_enhance+6].product.subProducts)
            }
        }
    }

    const extractSubProduct = (subProducts: any) => {
        return subProducts ? subProducts[0] : null
    }

    const AddMatrixValues = (matrix: any) => {
        return matrix.reduce((r:any, a:any) => a.map((b:any, i:any) => (r[i] || 0) + b), []);
    }

    return (
        <div className={B()}>
            
            <div className={E('modal')}>
                <Icon name="saved"/>
                
                <h2>Finished</h2>

                <Button text="Save and Next" icon="right-arrow-circle-white" click={gotoAnamnesis}/>

                <RouterLink className={E('modify')} click={modify}>
                    Modify my answers
                </RouterLink>
            </div>

            <div className={E('footer')}>
                <p> Cras tristique libero libero. Etiam maximus nec lorem fermentum ullamcorper. Integer ultrices laoreet felis, non laoreet ante vehicula nec. </p>
                <div>
                    <Button text="Questionnaire summary" type="secondary" icon="right-arrow-circle"  click={gotoAnamnesis}/>
                    <Button text="More questions" icon="right-arrow-circle-white" click={goToQuestionnaire}/>
                </div>
            </div>
        </div>
    )
}

export default NeedExplorerGeneralCheckPoint;