import React, { useEffect, useState } from 'react';
import Button from '../../../components/ui/Button';

interface Props {
    isVisible: boolean,
    onSelect: (type: 'qrcode' | 'id' | 'waiting-list') => void
}

const StartFrom : React.FC<Props>  = ({
    isVisible = true,
    onSelect
}) =>  {
    // VARIABLES
    const [isShow, setShow] = useState(Boolean);

    // HOOKS
    useEffect(() => {

        const delay = isVisible ? 750 : 0;
        setTimeout(() => {
            setShow(isVisible);
        },delay);
    }, [isVisible]);

    // METHODS

    return (
        <div className={`start-from ${!isShow ? 'start-from--hidden' : ''}`}>
            <h5>Start from</h5>

            {/* <Button 
                outlined={false}
                click={() => onSelect('waiting-list')}
                icon="qrcode"
                text="Waiting List" 
                type="secondary"/>  */}

            <Button 
                textSize="small"
                outlined={false}
                click={() => onSelect('qrcode')}
                icon="qrcode"
                text="QR Code" 
                type="secondary"/> 
            <Button 
                textSize="small"
                outlined={false}
                click={() => onSelect('id')}
                icon="stacked-group"
                text="ID" 
                type="secondary"/>
        </div>
    )
}

export default StartFrom;